import { Injectable } from '@angular/core';
import { AddressFacade, AddressUnit, Municipality } from '@sales/shared/address/data-access';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class AddressFormFieldFacade {
  constructor(private readonly addressFacade: AddressFacade) {}

  getMunicipalities(): Observable<Municipality[]> {
    return this.addressFacade.getMunicipalities();
  }

  getStreets(query: string, postalCodeQuery: string): Observable<AddressUnit[]> {
    return this.addressFacade.getStreets(query, postalCodeQuery);
  }

  getGeographicalAddresses(query: string, streetAddressUnitId: string): Observable<AddressUnit[]> {
    return this.addressFacade.getGeographicalAddresses(query, streetAddressUnitId);
  }
}
